.header__search-bar, .content__container .content {
  margin: auto;
  max-width: 64em;
  width: 100%; }

.text-center {
  text-align: center; }

.main {
  overflow-x: unset !important; }

.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  text-align: center;
  padding: 0 2em !important;
  z-index: 20;
  background: #f0f2f5 !important;
  box-shadow: 0 0 20px #888888; }

.content__container {
  padding: 2em;
  min-height: 100vh; }
  .content__container .content {
    min-height: 100vh;
    margin-top: 2em; }

.sider__brand {
  text-align: center;
  padding: 1em 0; }
  .sider__brand__avatar {
    color: #001529; }

html, body, #root {
  width: 100%;
  height: 100%;
}

